.profile{
    display: flex;
}

.profileRight{
    flex: 9;
}

.profileCover{
    height: 320px;
    position: relative;
}

.profileCoverImg{
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileUserImg{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 150px;
    border: 3px solid white;
}

.profileInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profileInfoName{
    font-size: 24px;
}

.profileInfoDesc{
    font-weight: 300;
}

.profileRightBottom{
    display: flex;
}

.updatePhotoButton{
    margin-top: 10px;
    align-items: center;
    background-color: green;
    color: white;
    font-weight: bold;
    border-radius: 5px;
}

.editButtons{
    margin-top: 5px;
}

.editBtn{
   border-radius: 5px;
   margin-left: 5px;
}

.editBtn:hover{
    cursor: pointer;
    transform: scale(1.1);
    border-color: green;
    

}